@import url(https://fonts.googleapis.com/css2?family=Work+Sans:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


html, body {
  
  width: 100%;
  height:100%;
  margin:0;
  padding:0;
  /* overflow-x:hidden; */
}

body::-webkit-scrollbar {
  display: none;
}

body {
    scroll-behavior: smooth;
    background: #fef6e4;
    background-size: 400% 400%;
    animation: gradient 10s ease infinite;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

h4{
  margin-left: 15px;
  margin-bottom: 0%;
  margin-top: 5px;
}

.custom-shape-divider-top-1634922272 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  line-height: 0;
}

.custom-shape-divider-top-1634922272 svg {
  position: relative;
  display: block;
  width: calc(100% + 1.3px);
  height: 126px;
}

.custom-shape-divider-top-1634922272 .shape-fill {
  fill: #FFFFFF;
}

.header {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 100vh;
}

.intro-name{
  color: #172c66d0 !important;
  text-decoration: underline;

}
.header h1 {
  font-family: "Work Sans", sans-serif;
  text-shadow: 0px 2px 3px #b8beca;
  margin-bottom: 0px;
  letter-spacing: 2px;
}
.description {
  margin-top: 5px;
  font-family: "Work Sans", sans-serif;
  color: #585e81;
  font-size: 14px;
  padding-top: 10px;
  font-weight: 500;
}

.intro {
  color: #001858;
  text-align: "left";
}

.enter {
  -webkit-filter: invert(62%) sepia(56%) saturate(510%) hue-rotate(294deg) brightness(96%) contrast(100%);
          filter: invert(62%) sepia(56%) saturate(510%) hue-rotate(294deg) brightness(96%) contrast(100%);
  font-weight: bold;
  margin-top: 70px;
  cursor: pointer;
  padding: 5px 30px;
}

@media only screen and (max-width: 600px) {
  .intro {
    padding: 0 5%;
  }
}

.project-card {
  margin: 10px;
  transition: 0.3s;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  background-color: white;
  width: 35%;
  border-radius: 20px;
}
.project-info {
  padding: 20px 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  box-sizing: border-box;
  height: 100%;
}

.project-img {
  width: 30%;
  height: 100%;
  object-fit: cover;
  object-position: 20% 50%;
  border-top-left-radius: 2%;
  border-bottom-left-radius: 2%;
}

.project-header{
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-self: flex-start;
}

.header-icon{
  -webkit-filter: invert(15%) sepia(11%) saturate(7251%) hue-rotate(187deg) brightness(101%) contrast(99%);
          filter: invert(15%) sepia(11%) saturate(7251%) hue-rotate(187deg) brightness(101%) contrast(99%);
}

.github-icon, .webasset-icon, .figma-icon{
  -webkit-filter: invert(72%) sepia(18%) saturate(5987%) hue-rotate(299deg) brightness(111%) contrast(92%);
          filter: invert(72%) sepia(18%) saturate(5987%) hue-rotate(299deg) brightness(111%) contrast(92%);
}

.project-title {
  font-family: 'Work Sans', sans-serif;
  font-size: 25px;
  text-align: left;
  color: #0A315D;
  font-weight: 700;

}

.project-card:hover {
  transform: scale(1.05);
}

.desc {
  font-family: 'Work Sans', sans-serif;
  color: rgb(53, 47, 47);
  position: relative;
  padding-top: 15px;
  padding-bottom: 15px;
  margin: 0;
  text-align: left;
}

.technologies {
  font-family: 'Open Sans', sans-serif;
  color: rgb(133, 113, 113);
  font-weight: 600;
  background: rgb(255, 255, 255);
  text-align: center;
}

.project-tag{
  margin-right: 5px;
  margin-top: 5px;
}

.card-footer {
  justify-self: flex-start;
  bottom: 10px;
}

.source:hover {
cursor: pointer;
}


@media only screen and (max-width: 600px){
  .project-card {
    width: 100%;
  }
}
.sidebar-com {
  position: absolute;
  float: right;
  display: flex;
  flex-direction: column;
  top: 50%;
  left: 96%;
  transform: translate(-50%, -50%);
  position: fixed;
  z-index: 100;
}

button {
  outline: none;
  border: none;
}

.button {
  background-color: #f582ae;
  margin: 5px;
  margin-right: 30px;
  border-radius: 100%;
  padding: 10px;
  opacity: 0.7;
  transition: 0.5s;
}

.group {
  display: flex;
  flex-direction: row-reverse;
}

.sidebar {
  font-family: "Work Sans", sans-serif;
  display: inline;
  font-size: 20px;
  color: #001858;
  margin-right: 10px;
  opacity: 0;
  transition: 0.4s;
}

.button:hover + .sidebar {
  display: block;
  opacity: 1;
}

.button:hover,
.button:hover {
  cursor: pointer;
  opacity: 1;
}

/* tablet and phone screens will NOT show the sidebar */
@media screen and (max-width: 1240px) {
  .sidebar-com {
    display: none;
  }
}

.projects{
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
  }
  
  .project-page-header{
    padding-top: 100px;
    margin-bottom: 0px;
    letter-spacing: 2px;
    
  }
  
  .projects-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 2%;
  }
  
  .project-page{
    display:flex;
    align-items: center;
    flex-direction: column;
    min-height: 80vh;
  }
  
  .project-page h1{
    color:#001858;
    font-family: 'Work Sans', sans-serif;
    text-shadow: 0px 2px 3px #b8beca;
    font-size: 60px;
    padding-top: 0;
  }
  
  
  @keyframes onHover{
    0%{
      opacity: 0px;
    }
    100%{
      opacity: 100%;
    }
  }

  @media screen and (min-width: 1240px){
      .projects-container{
          padding-right: 10%;
          padding-left: 10%;
      }
  }
.fade-in-section {
    opacity: 0;
    transform: translateY(20vh);
    visibility: hidden;
    transition: opacity 0.2s ease-out, transform 0.6s ease-out;
    will-change: opacity, visibility;
  }
  
  .fade-in-section.is-visible {
    opacity: 1;
    transform: none;
    visibility: visible;
    display: flex; 
  }
.about {
  min-height: 50vh;
  max-width: 70vh;
  margin: auto;
}
.box {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: center;
  padding: 30px;
}

.about h1 {
  color: #001858;
  font-family: "Work Sans", sans-serif;
  text-shadow: 0px 2px 3px #b8beca;
  font-size: 60px;
  text-align: center;
  margin-bottom: 0;

}

.about-description {
  font-family: "Work Sans", sans-serif;
  font-size: 20px;
  font-weight: 600;
  color: #4b506d;
  padding-left: 20px;
}

.about-linkedin-icon, .about-github-icon, .about-email-icon {
  -webkit-filter: invert(25%) sepia(13%) saturate(1686%) hue-rotate(193deg) brightness(95%) contrast(82%);
          filter: invert(25%) sepia(13%) saturate(1686%) hue-rotate(193deg) brightness(95%) contrast(82%);
}
.about-links{
  display: flex;
  justify-content: center;
  align-items: center;
}
.header-svg{
    position: fixed; left: 0; right: 0; top: 0; bottom:auto;
}
